.list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.post {
    width: 100%;
    border: 2px solid red;
    border-radius: 5px;
    padding: 20px;
}

.str {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
}

.image {
    width: 200px;
}
